import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function quickRatioTtm(tableName: string) {
  const title = "Quick Ratio (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["quickRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Can the company pay short-term bills without selling inventory?",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about its quick or current ratio?`,
              value: info.row.original.financialData.companyRatios?.data?.quickRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.quickRatioTtm}
        />
      )
    },
  })
}

export { quickRatioTtm }
